import React from 'react';
import { connect } from 'react-redux';

import { setScreen } from '../../../../../actions/appActions';
import { Popups } from '../../../../../enums.ts';

import Pause from './Pause';
import Score from './Score';
import Info from './Info';

@connect(
  state => ({
    game: state.game,
    level: state.level
  }),
  dispatch => ({
    dispatch
  })
)
class GamePopups extends React.Component {
  setScreen = screen => {
    const { dispatch } = this.props;
    dispatch(setScreen(screen));
  };

  render() {
    const { game, level } = this.props;
    let popup;

    switch (game.currentPopup) {
      case Popups.Pause:
        popup = <Pause setScreen={this.setScreen} />;
        break;
      case Popups.Score:
        popup = <Score setScreen={this.setScreen} game={game} />;
        break;
      case Popups.Info:
        popup = <Info level={level} />;
        break;
      default:
        return null;
    }

    return (
      <div className="game-popup">
        <div className="game-popup-container">{popup}</div>
      </div>
    );
  }
}

export default GamePopups;
