/* eslint-disable import/no-cycle */
import * as PIXI from 'pixi.js';

import IGameObject from '../IGameObject';
import Position from '../Position';
import BoxCollider from '../../BoxCollider';
import game from '../../Game';
import { TileLayers } from '../../../enums';

class WaterTile implements IGameObject {
  public boxCollider: BoxCollider;

  private tileX: number;
  private tileY: number;

  constructor({ x, y, tileX, tileY }) {
    this.boxCollider = new BoxCollider({ width: 16, height: 16, position: new Position({ x, y }) });

    this.tileX = tileX;
    this.tileY = tileY;
  }

  public init = (): void => {};

  public update = (): void => {};

  public render = (): void => {};

  public destroy = (): void => {};

  public pause = (): void => {};

  public resume = (): void => {};

  public setWaterFilter = (filters: PIXI.Filter[]): void => {
    // Add filters to current waterTile
    game.currentLevel.tileLayer.setTileFilter(
      this.tileX,
      this.tileY,
      filters,
      TileLayers.TopAssets1
    );
  };

  public clearWaterFilter = (): void => {
    // Remove filters from current water tile
    game.currentLevel.tileLayer.clearTileFilter(this.tileX, this.tileY, TileLayers.TopAssets1);
  };

  // Check if object is an instance of this object (WaterTile)
  public static isWaterTile(object: any): object is WaterTile {
    return 'setWaterFilter' in object;
  }
}

export default WaterTile;
