import React from 'react';

const Button = ({ onClick, children, fullwidth, className }) => {
  const classes = ['button'];

  if (fullwidth) {
    classes.push('full-width');
  }

  return (
    <div
      onClick={onClick}
      className={`${classes.join(' ')} ${className}`}
      role="button"
      onKeyDown={onClick}
      tabIndex={0}
    >
      {children}
    </div>
  );
};

export default Button;
